.hamburger {
  height: 60px;
  width: 60px;
  padding: 0.6rem;
  padding-bottom: 30px;
  padding-right: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hamburger > div {
  position: relative;
  top: 0;
  left: 0;
  background: #e2e4e6;
  height: 2px;
  width: 100%;
  transition: all 0.4s ease;
}

.hamburger > div::before,
.hamburger > div::after {
  content: "";
  position: absolute;
  top: -10px;
  background: rgb(255, 241, 241);
  width: 100%;
  height: 2px;
  transition: all 0.4s ease;
}

.hamburger > div::after {
  top: 10px;
}



.bgCover {
  padding-top: 25.62%;
  position: relative;
  overflow: hidden;
}
.bgCover > img {
  height: 100%;
  width: 100%;
  top: 0px;
  object-fit: cover;
  position: absolute;
}
.bgCover::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}

