.btn2 {
  position: relative;
  bottom: 100px;
}
.btn2 > a {
  display: flex;
  border: 2px solid #008c45;
  width: 250px;
  height: 50px;
  align-items: center;
  flex-wrap: nowrap;
  position: relative;
  margin: 0 auto;
  text-decoration: none;
}
a::before {
  position: absolute;
  content: "";
  right: 0px;
  width: 0px;
  height: 100%;
  background-color: #008c45;
  transition: width 0.5s ease 0s;
}
a > span {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-family: "Poppins", sans-serif;
  font-size: 20px;
  line-height: 26px;
  font-weight: light;
  z-index: 1;
  color: #008c45 !important;
}

.arrow {
  display: flex;
  align-items: center;
  height: 100%;
  width: 40%;
  z-index: 1;
  background-color: #008c45;
  font-size: 20px !important;
  color: rgb(41, 4, 4) !important;
  font-weight: 300 !important;
}

