.abstractBg {
  width: 100%;
  padding-bottom: 80px;
  overflow: hidden;
  position: relative;
}

.abstractBg > img {
  object-fit: cover;
  background-position: left;
  background-repeat: no-repeat;
  top: 0px;
  position: absolute;
}

.Title {
  margin-top: 80px;
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 36px;
  font-weight: 500;
  color: #314550;
  text-transform: capitalize;
}
.details {
  margin-top: 40px;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  text-align: center;
  text-transform: capitalize;
  color: black;
}


.read {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  text-align: center;
  margin-top: 30px;
  color: #008c45;
  cursor: pointer;
}
